.cardBody {
  width: 285px;
  height: 285px;
  margin: 1px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: black;
  border-radius: 3px;
  margin: 1%;

  h3 {
    font-weight: 300;
    display: block;
    margin: 10px;
    text-align: center;
  }

  p {
    width: 80%;
  }

  .image {
    height: auto;
    width: auto;
    max-width: 170px;
    max-height: 170px;
  }
}
