.logo{
  width: 100%;
  margin-left: 7px;
  opacity: 0.2;
}

.logo__link{

  &__name{
    
      font-size: 1rem;
      font-weight: 300;
      text-transform: uppercase;
      text-align: center;
      justify-content: center;
      position: relative;
      bottom: 0.43em;
    
  }
}


svg{
  width: 1.5rem;
  height: 1.5rem;
  transform: rotate(180deg);
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
    color: black; 
    text-decoration:none; 
    cursor:pointer;  
}


.faPrimary {
  opacity: 0.5;
}

@media screen and (min-width: 481px){

  svg {
    width: 2.3rem;
    height: 2.3rem;
  }

  .logo__link__name{
    font-size: 1.5rem;
    bottom: 0.35em;
  }
}


@media screen and (min-width: 768px){
  
  svg {
    width: 3.5rem;
    height: 3.5rem;
  }

  .logo__link__name{
    font-size: 2.5rem;
  }
  
}

@media screen and (min-width: 991px){
  
  svg {
    width: 4.2rem;
    height: 4.2rem;
  }

  .logo__link__name{
    font-size: 3rem;
  }



}

